import React, { Component } from 'react';
import { Input, Button} from '@material-ui/core';
import "./Home.css"
import {setSession,getSession} from "./sessions";

class Home extends Component {
  	constructor (props) {
		super(props)
		this.state = {
			url: ''
		}
	}

	componentDidMount()
	{

		var senha2=window.localStorage.getItem("senha");
		console.log(senha2)
		var senha=window.prompt("senha")
		if(senha===senha2)
		{
			alert("logado com sucesso!")
		}
		else
		{
			alert("senha errada")
			window.location.href="/"
		}
	}

	handleChange = (e) => this.setState({ url: e.target.value })

	join = () => {
		if (this.state.url !== "") {
			var url = this.state.url.split("/")
			window.location.href = `/${url[url.length-1]}`
		} else {
			var url = Math.random().toString(36).substring(2, 7)
			window.location.href = `/${url}`
		}
	}
	alert = () => {
      setSession("TRIGO");
	}

	show = () => {
		alert(getSession());
	  }

	render() {
		return (
			<div className="container2">
				{/*<div style={{fontSize: "14px", background: "white", width: "10%", textAlign: "center", margin: "auto", marginBottom: "10px"}}>
					Source code: 
					<IconButton style={{color: "black"}} onClick={() => window.location.href="https://github.com/0x5eba/Video-Meeting"}>
						<GitHubIcon />
					</IconButton>
		        </div>*/}
				
				<div>
					<h1 style={{ fontSize: "45px" }}>Video Aulas</h1>
					<p style={{ fontWeight: "200" }}>Video conferencia.</p>
				</div>

				<div style={{
					background: "white", width: "30%", height: "auto", padding: "20px", minWidth: "400px",
					textAlign: "center", margin: "auto", marginTop: "100px"
				}}>
					<p style={{ margin: 0, fontWeight: "bold", paddingRight: "50px" }}>Iniciar ou Entrar em uma aula</p>
					<Input placeholder="URL" onChange={e => this.handleChange(e)} />
					<Button variant="contained" color="primary" onClick={this.join} style={{ margin: "20px" }}>Entrar</Button>
				</div>
			</div>
		)
	}
}

export default Home;